<template>
  <transition name="fade">
    <!--loding s-->
    <div v-if="isShow" ontouchmove="return false;" class="loadingbg">

      <div class="demo-image__preview">
        <div class="rightEye">
        <el-divider  class="divideLine" content-position="left">OD(右眼)</el-divider>
        <div class="block"  v-for="(item,index) in right_srcList" :key="index">
          <el-radio   class="checkbox"  v-model="radio2" :label="index" @change="handleClickR(index)">入报告</el-radio>
          <el-image
            :src="item"
            :preview-src-list="[item]"
          ></el-image>
        </div>
        </div>

        <div class="leftEye">
        <el-divider  class="divideLine" content-position="left">OS(左眼)</el-divider>
        <div class="block" v-for="(item,index) in left_srcList" :key="index">
          <el-radio  class="checkbox"  v-model="radio1" :label='index'  @change="handleClickL(index)">入报告</el-radio>
          <el-image
            :src="item"
            :preview-src-list="[item]"
          ></el-image>
        </div>
      </div>
      </div>

      <div class="button_area">
        <el-button @click="click_confirm">确认</el-button>
        <el-button @click="get_back">返回</el-button>
      </div>

    </div>
  </transition>
</template>

<script>
  export default {
    data () {
      return {
        isShow: false,
        text: '努力加载中',
        left_src: 'http://39.104.171.127:8082/media/patient_img/7_Right_57e_origin_0.jpg',
        right_src: 'http://39.104.171.127:8082/media/patient_img/7_Right_57e_origin_0.jpg',
        right_srcList:['http://39.104.171.127:8082/media/patient_img/7_Right_57e_origin_0.jpg'],
        left_srcList:['http://39.104.171.127:8082/media/patient_img/7_Right_57e_origin_0.jpg'],
        radio1:0,
        radio2:0,
        cur_srcL:'',
        cur_srcR:'',
      }
    },
    mounted(){
      this.bus.$on('displayAllPictures',msg => {
        this.data_process(msg);
      })

    },

    methods:{
      get_back:function () {
        this.$displayPicture(false)
      },

      click_confirm:function(){
        let pic_dic = {};
        pic_dic['left_src'] = this.cur_srcL;
        pic_dic['right_src'] = this.cur_srcR;
        this.$displayPicture(false);
        this.bus.$emit('modify_scr', pic_dic);
      },

      data_process(msg){
        this.radio1 = 0;
        this.radio2 = 0;
        this.cur_srcL = msg['left_srcList'][0];
        this.cur_srcR = msg['right_srcList'][0];
        this.left_srcList = msg['left_srcList'];
        this.right_srcList = msg['right_srcList'];
      },

      handleClickL(index){
        this.cur_srcL = this.left_srcList[index];
      },

      handleClickR(index){
        this.cur_srcR = this.right_srcList[index];
      },
    },
  }
</script>

<style  scoped>
  /*.transform(@a) {*/
    /*-moz-transform: @a;*/
    /*-webkit-transform: @a;*/
    /*-ms-transform: @a;*/
    /*transform: @a;*/
  /*}*/
  /*.transition( @a ) {*/
    /*-moz-transition: @a;*/
    /*-webkit-transition: @a;*/
    /*-ms-transition: @a;*/
    /*transition: @a;*/
  /*}*/

  .fade-enter-active,
  .fade-leave-active {
    transition: opacity 0.1s;
  }

  .fade-enter,
  .fade-leave-to {
    opacity: 0;
  }

  .loadingbg {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    overflow-x:hidden;
    overflow-y:scroll;
    z-index: 9999;
    background-color: rgba(0, 0, 0, 1);
  }
  .loadingbg .loading {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 71px;
    height: 71px;
    margin: -33px 0 0 -33px;
  }
  .loadingbg .loading .loading_icon {
    display: block;
    width: 36px;
    height: 36px;
    background: url(//appnew.ly.com/hc/1/img/icon_tg_loading1.gif) no-repeat;
    background-size: 36px 36px;
    margin: 0 auto;
  }
  .loadingbg .loading .loading_text {
    font-size: 14px;
    margin-top: 12px;
    text-align: center;
    color: #999;
    font-family: "Microsoft YaHei", "微软雅黑", Arial, SimSun, sans-serif;
  }

  .block{
    width: 240px;
    height: 180px;
    margin: 1em 0 0 1em;
  }


.demo-image__placeholder::after {
    content:"";
    display:table;
    clear:both;
  }

 .block{
  float: left;
}
  .rightEye{
    overflow: hidden;
  }
  .checkbox{
    width: 50px;
    height:50px;
  }
  .button_area{
    position: absolute;
    bottom: 20px;
    right: 20px;
  }
  .checkbox{
    position: absolute;
    z-index: 1000;
    color:white;
  }

</style>

<style>
  i.el-icon-circle-close{
    color: darkgray;
  }
</style>



