import displayOicture from './index.vue'
export default {
  install: vue => {
    const MyLoading = vue.extend(displayOicture)
    const instance = new MyLoading()
    instance.$mount(document.createElement('div'))
    document.body.appendChild(instance.$el)
    vue.prototype.$displayPicture = (bool, text) => {
      instance.isShow = bool
      instance.text = text || "正在加载中"
    }
  }
}



